	    /**
	     * parse javascript object to json string
	     * @param  {Object} object
	     * @return {String} json string
	     */
	    this.stringifyJSON = function(obj) {
	        // Attempt to parse using the native JSON parser first
	        if (window.JSON && window.JSON.stringify) {
	            return window.JSON.stringify(obj);
	        }
	        switch (typeof (obj)) {
	            case 'string':
	                return '"' + obj.replace(/(["\\])/g, '\\$1') + '"';
	            case 'array':
	                return '[' + obj.map(that.stringifyJSON).join(',') + ']';
	            case 'object':
	                if (obj instanceof Array) {
	                    var strArr = [];
	                    var len = obj.length;
	                    for (var i = 0; i < len; i++) {
	                        strArr.push(that.stringifyJSON(obj[i]));
	                    }
	                    return '[' + strArr.join(',') + ']';
	                } else if (obj === null) {
	                    return 'null';
	                } else {
	                    var string = [];
	                    for (var property in obj) {
	                        if (obj.hasOwnProperty(property)) {
	                            string.push(that.stringifyJSON(property) + ':' + that.stringifyJSON(obj[property]));
	                        }
	                    }
	                    return '{' + string.join(',') + '}';
	                }
	                break;
	            case 'number':
	                return obj;
	            case false:
	                return obj;
	            case 'boolean':
	                return obj;
	        }
	    };
	
