	            var paddingBottom = parseInt($target.css('padding-bottom'), 10);
	
	            // 计算初步的结果
	            var resultHeight = height + paddingTop + marginTop + paddingBottom + marginBottom;
	            var resultTop = top + menuContainer.height();
	            
	            // var spaceValue;
	
